import React from "react";
import PropTypes from "prop-types"

const Card = ({lang, url}) => {
	return(
		<div className="card">
			<section>
				<img src={url} />
			</section>
			<h2> {lang} </h2>
		</div>
	)
}

Card.propTypes = {
	lang: PropTypes.string,
	url: PropTypes.string
}

export default Card;