import React from "react"
import Link from "./Link"
import "./stylesCom/nav.css"

const Nav = () => {
	
	const links = ["Inicio", "Ejemplo"];

	return(
		<div className="nav">
			<section>
				<span>React!</span>
			</section>
			<section>
				{
					links.map((p, i) => {
						return <Link key={i} text={p} />
					})
				}
			</section>
		</div>
	)
}

export default Nav;